.btn {
  display: -webkit-inline-box;
  display: -ms-inline-flexbox;
  display: inline-flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  height: 50px;
  padding: 0 2rem;
  background-color: #5041b2;
  color: #fbfcfd;
  font-size: 0.9rem;
  font-weight: 900;
  letter-spacing: normal;
  white-space: nowrap;
  border: 3px solid transparent;
  border-radius: 6px;
  -webkit-transition-duration: 0.3s;
          transition-duration: 0.3s;
}

.btn.white-btn {
  border-color: #fbfcfd;
  background-color: transparent;
}

.btn.disabled {
  background-color: #eaeaea;
  opacity: 1;
}

.btn:hover, .btn:active, .btn:focus {
  background-color: #fbfcfd;
  color: #5041b2;
  border-color: #5041b2;
}

.hidden-txt {
  visibility: hidden;
  position: absolute;
  top: -9999px;
  left: -9999px;
  z-index: -9999;
  text-indent: -9999px;
}

.animate-show {
  opacity: 0;
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
}

.animate-show.animated {
  opacity: 1;
}

.animate-slide {
  width: 0;
  overflow: hidden;
  -webkit-transition: all 1.2s;
  transition: all 1.2s;
  white-space: nowrap;
}

.animate-slide.animated {
  width: 100%;
}

.animate-stick {
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
  -webkit-transform: translateY(20px) rotate(3deg);
  transform: translateY(20px) rotate(3deg);
  opacity: 0;
}

.animate-stick.animated {
  -webkit-transform: translateY(0);
  transform: translateY(0);
  opacity: 0;
}

.animate-skew {
  position: absolute;
  top: 0;
  left: 0;
  width: calc(100% + 400px);
  height: 100%;
  background-color: #fbfcfd;
  -webkit-transition: all 1000ms cubic-bezier(0.694, 0, 0.335, 1);
  transition: all 1000ms cubic-bezier(0.694, 0, 0.335, 1);
  -webkit-transform: translateX(-200px) skew(-20deg);
  transform: translateX(-200px) skew(-20deg);
  z-index: 2;
}

.animate-skew.animated {
  -webkit-transform: translateX(100%);
  transform: translateX(100%);
}

.animate-drop {
  -webkit-transform: translateY(-30px) rotate(-5deg);
  transform: translateY(-30px) rotate(-5deg);
  -webkit-transition: all 0.65s cubic-bezier(0.694, 0, 0.335, 1);
  transition: all 0.65s cubic-bezier(0.694, 0, 0.335, 1);
  opacity: 0;
}

.animate-drop.animated {
  -webkit-transform: translateY(0);
  transform: translateY(0);
  opacity: 1;
}

